import React from 'react'
import ContactItem from '../Components/ContactItem';
import phone from '../img/phone.svg';
import email from '../img/emailme.svg';
import location from '../img/location.svg';
import Tittle from '../Components/Tittle';

function ContactPage() {
    return (
        <div>
            <div className="title">
                <Tittle title={'Contact Me'} span={'Contact Me'} />
            </div>
            <div className="ContactPage">
                <div className="map-sect">
                    <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3987.7664566641542!2d103.32250031457325!3d1.8376124986601872!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x31d0631285aa6803%3A0x1fd6021e66c4bcd6!2sJalan%20Renggam%20%26%20Lorong%20Jaya%2C%20Kampung%20Batu%206%201%2F2%2C%2086200%20Simpang%20Renggam%2C%20Johor!5e0!3m2!1sen!2smy!4v1633438230938!5m2!1sen!2smy" width="600" height="450" frameBorder="0" style={{border:0}} allowFullScreen="" aria-hidden="false" tabIndex="0"></iframe>
                </div>
                <div className="contact-sect">
                    <ContactItem icon={phone} text1={'011-51729230'}  title={'Phone'}/>
                    <ContactItem icon={email} text1={'rosshamiraazie@gmail.com'}  title={'Email'}/>
                    <ContactItem icon={location} text1={'No 11, Jalan Berlian 25, Taman Renggam Jaya 86200 Simpang Renggam'}  title={'Address'}/>
                </div>
            </div>
        </div>
    )
}

export default ContactPage;


