import React from 'react';
import about from '../img/coder.jpg';

function ImageSection() {
    return (
        <div className="ImageSection">
            <div className="img">
                <img src={about} alt=""/>
            </div>
            <div className="about-info">
                <h4>I am<span> Rosshahamira Azie Roslan</span></h4>
                <p className="about-text">
                I am currently pursuing B.Science with Honours (Information Technology) from Universiti Utara Malaysia.
                Since my childhood, i love art and design. I always try to design stuff with unique point of view. I also 
                love to create things that can be useful to others. 
                </p>
                <p className="about-text">
                I started coding since I was in University. Coding is also an art for me. I love it and now I have the 
                opportunity to design along with the coding. I find it really interesting and I enjoyed the process a 
                lot. 
                </p>
                <p className="about-text">
                My vision is to make the world better place. Now almost everything is becoming better than ever. It is time 
                for us to create more good stuff that helps the world to become a better place. 
                </p>
                <div className="about-details">
                    <div className="left-section">
                        <p>Full Name</p>
                        <p>Age</p>
                        <p>Languages</p>
                        <p>Address</p>
                        <p>Countries</p>
                    </div>
                    <div className="right-section">
                        <p>: Rosshahamira Azie Binti Roslan</p>
                        <p>: 24 years old</p>
                        <p>: Malay, English</p>
                        <p>: Simpang Renggam, Johor</p>
                        <p>: Malaysian</p>
                    </div>
                </div>
                <button className="btn">Download Cv</button>
            </div>
        </div>
    )
}

export default ImageSection;
