import React from 'react'
import Tittle from '../Components/Tittle'
import ImageSection from '../Components/ImageSection';
import SkillsSection from '../Components/SkillsSection';
import ServicesSection from '../Components/ServicesSection';
import design from '../img/design.svg';
import intelligence from '../img/intelligence.svg';
import gamedev from '../img/game-dev.svg';

function AboutPage() {
    return (
        <div className="AboutPage">
            <Tittle title={'About Me'} span={'About Me'} />
            <ImageSection />
            <Tittle title={'My Skills'} span={'My Skills'} />
            <div className="skillsContainer">
                <SkillsSection skill={'PHP HTML'} progress={'60%'} width={'60%'} />
                <SkillsSection skill={'React Js'} progress={'40%'} width={'30%'} />
                <SkillsSection skill={'Power BI'} progress={'50%'} width={'70%'} />
                <SkillsSection skill={'Power Apps'} progress={'60%'} width={'80%'} />
                <SkillsSection skill={'Laravel'} progress={'70%'} width={'90%'} />
                <SkillsSection skill={'Python'} progress={'30%'} width={'40%'} />
                <SkillsSection skill={'Java'} progress={'40%'} width={'80%'} />
                <SkillsSection skill={'Web Design'} progress={'45%'} width={'25%'} />
                <SkillsSection skill={'UI/Ux Design'} progress={'66%'} width={'76%'} />
            </div>

            <Tittle title={'Services'} span={'Services'} />
            <div className="servives-container">
                <ServicesSection image={design} title={'Web Design'} 
                text={'I do UI/UX design for the website that helps website to get a unique look'}
                />
                <ServicesSection image={intelligence} title={'Web Development'} 
                text={'I also develop the websites. I create high performance website with blazing fast speed.'}
                />
                <ServicesSection image={gamedev} title={'Game Development'} 
                text={'I develop game and create game with eye catching ui'}
                />

                
            </div>

        </div>
    )
}

export default AboutPage;
